@-webkit-keyframes joltDown {
  0% {-webkit-transform: translateY(-100%);transform: translateY(-100%); opacity: 0; height: 20px;}
  70% {-webkit-transform: translateY(-3px);transform: translateY(-3px); opacity: 1; height: 60px;}
  100% {-webkit-transform: translateY(-0px);transform: translateY(-0px); }
}

@keyframes joltDown {
  0% {-webkit-transform: translateY(-100%);transform: translateY(-100%); opacity: 0; height: 20px;}
  70% {-webkit-transform: translateY(-3px);transform: translateY(-3px); opacity: 1; height: 60px;}
  100% {-webkit-transform: translateY(-0px);transform: translateY(-0px); }
}

.header-aux-down {
  -webkit-animation: joltDown .4s ease-in-out;
          animation: joltDown .4s ease-in-out;
  -webkit-transform: translateY(-0px) !important;
      -ms-transform: translateY(-0px) !important;
          transform: translateY(-0px) !important;
  opacity: 1 !important;
  margin-top: -20px;
}

@-webkit-keyframes joltDownInit {
  0% {-webkit-transform: translateY(-100%);transform: translateY(-100%); opacity: 0; height: 20px;}
  50% {-webkit-transform: translateY(-100%);transform: translateY(-100%); opacity: 0; height: 20px;}
  85% {-webkit-transform: translateY(-3px);transform: translateY(-3px); opacity: 1; height: 60px;}
  100% {-webkit-transform: translateY(-0px);transform: translateY(-0px); }
}

@keyframes joltDownInit {
  0% {-webkit-transform: translateY(-100%);transform: translateY(-100%); opacity: 0; height: 20px;}
  50% {-webkit-transform: translateY(-100%);transform: translateY(-100%); opacity: 0; height: 20px;}
